import { NgModule , ModuleWithProviders, Component } from '@angular/core';
import { Routes , RouterModule } from '@angular/router';
import { AppComponent } from '../app.component'
import { GimsHomeComponent } from '../gims-home/gims-home.component';
import { GimsAppScreenComponent } from '../gims-app-screen/gims-app-screen.component';
import {LcaInitiationComponent} from '../lca-initiation/lca-initiation.component';
import {DefaultInitiateComponent } from '../default-initiate/default-initiate.component';
import { WorkProgressComponent } from '../work-progress/work-progress.component';
// GIMS-routes
const routes: ModuleWithProviders = RouterModule.forRoot([
    {path: '', redirectTo: '/WT_GIMS_E', pathMatch: 'full' },
    {path: 'WT_GIMS_E', component : GimsHomeComponent,
        children: [
                    {path: '', component: GimsAppScreenComponent,
                    children: [
                            {path: '', component: DefaultInitiateComponent},
                            { path: 'lca', component: LcaInitiationComponent}]
                    },
                    {path: 'knowledge', component: WorkProgressComponent},
                    {path: 'application', component: WorkProgressComponent},
                    {path: 'task', component: WorkProgressComponent},
                    {path: 'home', component: WorkProgressComponent },
                ]
    }
], { useHash: true});
@NgModule({
    imports: [routes],
    exports: [RouterModule],
    providers : []
})
export class GimsRoutingModule {

}



