import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import { Router } from '@angular/router';
@Component({
  selector: 'app-gims-app-screen',
  templateUrl: './default-initiate.component.html',
  styleUrls: ['./default-initiate.component.css']
})
export class DefaultInitiateComponent implements OnInit {
  isLinear = false;
  firstFormGroup: FormGroup;
  secondFormGroup: FormGroup;
  foods = [
    {value: 'steak-0', viewValue: 'India'},
    {value: 'pizza-1', viewValue: 'USA'},
    {value: 'tacos-2', viewValue: 'New York'}
  ];
  screenHint = `This is a one stop shop providing immigration assistance
   for all your business related travel and onsite assignments.
   This is a consolidated app, wherein you can create, edit and
   view visa/work permit application for self/reportee; upload supporting
    documents;check status and lifecycle of application; dashboard with overall reports
    on LCA initiation, availability and approval;pending task actions and a
    repository for immigration related information on any source destination or visa
     type as news and information.
     You can also take a print out of your application which is just a click away.`;
  constructor(
    private _formBuilder: FormBuilder,
    private _router: Router
  ) { }

  ngOnInit() {
    this.firstFormGroup = this._formBuilder.group({
      assignmentLoaction: ['', Validators.required],
      assignmentFor: ['', Validators.required],
      wiproDivision: ['', Validators.required],
      sourceCountry: ['', Validators.required],
      destCountry: ['', Validators.required],
      visaType: ['', Validators.required],
      startDate: ['', Validators.required],
      endDate: ['', Validators.required],
      assignmentAddress: ['', Validators.required],
    });
    this.secondFormGroup = this._formBuilder.group({
      secondCtrl: ['', Validators.required]
    });
  }
  navToLca() {
    this._router.navigate(['/WT_GIMS_E/lca']);
  }

}



