import { Injectable } from '@angular/core';
@Injectable()
export class GimsSharedService {
  public generalDetails: Object;
  constructor() { }
  public saveGeneralDetails(generalDetails: Object) {
  this.generalDetails = generalDetails;
  }
  public getGeneralDetails() {
         return this.generalDetails;
  }
}



