import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from '@angular/http';
import { FlexLayoutModule } from '@angular/flex-layout';
import {RouterModule, Routes} from '@angular/router';
import { AppComponent } from './app.component';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import 'hammerjs';
import { MaterialModule } from './core/material.module';
import { GimsHomeComponent } from './gims-home/gims-home.component';
import { GimsRoutingModule } from './core/gims-routing.module';
import { AppbarComponent } from './appbar/appbar.component';
import { SideNavMenuComponent } from './side-nav-menu/side-nav-menu.component';
import { GimsAppScreenComponent } from './gims-app-screen/gims-app-screen.component';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import { ProjectPickerComponent } from './project-picker/project-picker.component';
import { GeneralDetailsComponent } from './general-details/general-details.component';
import { GimsSharedService } from './shared-services/gims-shared-service.service';
import { LcaInitiationComponent } from './lca-initiation/lca-initiation.component';
import { DefaultInitiateComponent } from './default-initiate/default-initiate.component';
import { WorkProgressComponent } from './work-progress/work-progress.component';

@NgModule({
  declarations: [
    AppComponent,
    GimsHomeComponent,
    AppbarComponent,
    SideNavMenuComponent,
    GimsAppScreenComponent,
    ProjectPickerComponent,
    GeneralDetailsComponent,
    LcaInitiationComponent,
    DefaultInitiateComponent,
    WorkProgressComponent,
  ],
  imports: [
    BrowserModule,
    HttpModule,
    FlexLayoutModule,
    BrowserAnimationsModule,
    MaterialModule,
    GimsRoutingModule,
    FormsModule,
    ReactiveFormsModule
  ],
  providers: [GimsSharedService],
  bootstrap: [AppComponent]
})
export class AppModule { }



