import { Component, OnInit } from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';

@Component({
  selector: 'app-general-details',
  templateUrl: './general-details.component.html',
  styleUrls: ['./general-details.component.css']
})
export class GeneralDetailsComponent implements OnInit {
  foods = [
    {value: 'steak-0', viewValue: 'Steak'},
    {value: 'pizza-1', viewValue: 'Pizza'},
    {value: 'tacos-2', viewValue: 'Tacos'}
  ];
  generalForm: FormGroup;

  constructor(private _formBuilder: FormBuilder) { }
  ngOnInit() {
    this.generalForm = this._formBuilder.group({
      assignmentLoaction: ['', Validators.required],
      assignmentFor: ['', Validators.required],
      wiproDivision: ['', Validators.required],
      sourceCountry: ['', Validators.required],
      destCountry: ['', Validators.required],
      visaType: ['', Validators.required],
      managerEmpId: ['', Validators.required],
      AssignmentFor: ['', Validators.required],
      ApprovingAutority: ['', Validators.required], });
  }
  }



